body {
    @extend %flex;

    position: relative;

    overflow-x: hidden;

    flex-direction: column;

    min-width: 100%;
    min-height: 100%;
    margin: 0;

    font-family: $fontBase;
    font-size: $fontSizeBase;

    color: $colorBrandDark;

    * {
        box-sizing: border-box;
    }
    @include mq(lg) {
        background: purple;
    }
}


body.nav-stuck {
    .navMain {
        @include transition(top .2s);

        position: fixed;
        z-index: 1000;
        top: -75px;
        left: 0;

        width: 100%;
    }
}
