
//Font Stacks
$fontBase: 'Droid Serif', serif;
$fontHighlight: 'Montserrat', sans-serif;

//Font Sizes
$fontSizeBase: 16px!default;

$h1Size: 36px!default;
$h2Size: 30px!default;
$h3Size: 24px!default;
$h4Size: 20px!default;
$h5Size: 16px!default;
$h6Size: 14px!default;

$fontSizeXSm: 12px!default;
$fontSizeSm: 14px!default;
$fontSizeLg: 18px!default;
$fontSizeXLg: 22px!default;

$lineHeight: 26px!default;

//Monochrome Colors
$colorWhite: #fff;
$colorGreyLightest: darken($colorWhite, 12.5%);
$colorGreyLighter: darken($colorWhite, 25%);
$colorGreyLight: darken($colorWhite, 37.5%);
$colorGrey: darken($colorWhite, 50%);
$colorGreyDark: darken($colorWhite, 62.5%);
$colorGreyDarker: darken($colorWhite, 75%);
$colorGreyDarkest: darken($colorWhite, 87.5%);
$colorBlack: darken($colorWhite, 100%);

//Brand Colors
$colorBrandYellow: #fed156;
$colorBrandBlue: #52bad5;
$colorBrandDark: #5d5c5d;
$colorBrandMid: #b7b7b7;
$colorBrandLight: #f2f2f2;


//Spacing
$spacingBase: 14px;
$spacingXSm: $spacingBase/2.5;
$spacingSm: $spacingBase/2;
$spacingLg: $spacingBase*2;
$spacingXLg: $spacingBase*2.5;


//Grid
$gridWidthBase: (100%)/24;


//Media Queries
$startXSm: 1px!default;
$startSm: 480px!default;
$startMed: 641px!default;
$startLg: 851!default;
$startXLg: 1024px!default;
$startXXLg: 1440px!default;

$endXSm: $startSm - 1px;
$endSm: $startMed - 1px;
$endMed: $startLg - 1px;
$endLg: $startXLg - 1px;
$endXLg: $startXXLg - 1px;