.navMain {
    @extend %flex;

    @include transition(top .2s);

    position: relative;

    margin-bottom: 3px;

    font-family: $fontHighlight;
    font-size: $fontSizeLg;

    background: $colorBrandLight;

    &:after {
        position: absolute;
        top: 100%;
        left: 0;

        width: 100%;
        height: 3px;

        content: '';

        background-image: repeating-linear-gradient(to right,
        $colorBrandYellow, $colorBrandYellow 75px,
        $colorBrandBlue 75px, $colorBrandBlue 125px,
        $colorBrandDark 125px, $colorBrandDark 200px,
        $colorBrandMid 200px, $colorBrandMid 275px,
        $colorBrandBlue 275px, $colorBrandBlue 300px,
        $colorBrandYellow 300px, $colorBrandYellow 350px,
        $colorBrandDark 350px, $colorBrandDark 400px,
        $colorBrandMid 400px, $colorBrandMid 425px);
    }
    > ul {
        @extend %contained;
        @extend %unstyledList;
        @extend %flex;

        flex-direction: row;
        justify-content: space-around;

        font-weight: 300;
        line-height: $fontSizeLg;

        > li {
            position: relative;

            > a {
                @include transition(color);

                position: relative;
                z-index: 1;

                display: block;
                overflow: hidden;

                padding: $spacingBase $spacingLg;

                text-decoration: none;
                font-size: inherit;

                color: $colorBrandDark;

                > span {
                    @include transition(all .4s);

                    position: absolute;
                    z-index: -1;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    left: 0;

                    width: 0;
                    height: 0;
                    margin: auto;

                    transform: scale(0, 0);

                    opacity: 0;
                    border-radius: 50%;
                    background: darken($colorBrandBlue, 15%);
                }
            }
            &.has-children {
                > a {
                    &:before {
                        @extend %iconStyle;

                        @include transition;

                        margin: 0;
                        padding-right: 5px;

                        content: '\e808';
                        transform: rotate(0deg);
                        transform-origin: top left;
                    }
                }
            }
            > ul {
                @extend %unstyledList;
                @extend %flex;

                @include transition;

                position: absolute;
                z-index: 100;
                top: 100%;
                left: 0;

                flex-direction: column;

                min-width: 100%;
                padding: 0;

                transform: scale(1, 0);
                transform-origin: top;

                background: darken($colorBrandBlue, 10%);

                > li {
                    @include transition(background);

                    width: 100%;
                    padding: $spacingBase;

                    > a {
                        width: 100%;
                        height: 100%;

                        white-space: nowrap;
                        text-decoration: none;
                        font-size: inherit;

                        color: $colorBrandLight;
                    }
                    &:hover {
                        background: darken($colorBrandBlue, 20%);
                    }
                }
            }
        }
    }
}


.navMain {
    > ul {
        > li {
            &:hover {
                > a {
                    color: $colorBrandLight;

                    > span {
                        width: 150px;
                        height: 150px;

                        transform: scale(1.5, 1.5);

                        opacity: 1;
                    }
                }
                &.has-children {
                    > a {
                        &:before {
                            transform: rotate(90deg);
                        }
                    }
                    > ul {
                        transform: scale(1, 1);
                    }
                }
            }
        }
    }
}
