/* Slider */
.slick-slider {
    position: relative;

    display: block;

    box-sizing: border-box;

    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -khtml-user-select: none;

    -webkit-touch-callout: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-list {
    position: relative;

    display: block;
    overflow: hidden;

    margin: 0;
    padding: 0;
}
.slick-list:focus {
    outline: none;
}
.slick-list.dragging {
    cursor: pointer;
    cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    top: 0;
    left: 0;

    display: block;
}
.slick-track:before,
.slick-track:after {
    display: table;

    content: '';
}
.slick-track:after {
    clear: both;
}
.slick-loading .slick-track {
    visibility: hidden;
}

.slick-slide {
    display: none;
    float: left;

    height: 100%;
    min-height: 1px;
}
[dir='rtl'] .slick-slide {
    float: right;
}
.slick-slide img {
    display: block;
}
.slick-slide.slick-loading img {
    display: none;
}
.slick-slide.dragging img {
    pointer-events: none;
}
.slick-initialized .slick-slide {
    display: block;
}
.slick-loading .slick-slide {
    visibility: hidden;
}
.slick-vertical .slick-slide {
    display: block;

    height: auto;

    border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
    display: none;
}
/* Arrows */
.slick-prev,
.slick-next {
    position: absolute;
    z-index: 100;
    top: 50%;

    display: block;

    width: 30px;
    height: 30px;
    padding: 0;

    cursor: pointer;
    transform: translate(0, -50%);
    font-size: 0;
    line-height: 0;

    color: transparent;
    border: none;
    outline: none;
    background: transparent;
}
.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
    color: transparent;
    outline: none;
    background: transparent;
}
.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
    opacity: 1;
}
.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
    opacity: .25;
}

.slick-prev:before,
.slick-next:before {
    font-family: 'slick';
    font-size: 30px;
    -webkit-font-smoothing: antialiased;
    line-height: 1;

    opacity: .75;
    color: white;

    -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
    left: 25px;
}
[dir='rtl'] .slick-prev {
    right: 25px;
    left: auto;
}
.slick-prev:before {
    content: '←';
}
[dir='rtl'] .slick-prev:before {
    content: '→';
}

.slick-next {
    right: 25px;
}
[dir='rtl'] .slick-next {
    right: auto;
    left: 25px;
}
.slick-next:before {
    content: '→';
}
[dir='rtl'] .slick-next:before {
    content: '←';
}

/* Dots */
.slick-dotted.slick-slider {
    margin-bottom: 30px;
}

.slick-dots {
    position: absolute;
    bottom: -13px;

    display: block;

    width: 100%;
    margin: 0;
    padding: 0;

    list-style: none;

    text-align: center;
}
.slick-dots li {
    position: relative;

    display: inline-block;

    width: 22px;
    height: 22px;
    margin: 0 5px;
    padding: 0;

    cursor: pointer;
}
.slick-dots li button {
    display: block;

    width: 22px;
    height: 22px;
    padding: 0;

    cursor: pointer;
    font-size: 0;
    line-height: 22px;

    color: transparent;
    border: 0;
    outline: none;
    background: transparent;
}
.slick-dots li button:hover,
.slick-dots li button:focus {
    outline: none;
}
.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
    opacity: 1;
}
.slick-dots li button:before {
    position: absolute;
    top: 0;
    left: 0;

    width: 22px;
    height: 22px;

    content: '•';
    text-align: center;
    font-family: 'slick';
    font-size: 22px;
    -webkit-font-smoothing: antialiased;
    line-height: 22px;

    opacity: .25;
    color: black;

    -moz-osx-font-smoothing: grayscale;
}
.slick-dots li.slick-active button:before {
    opacity: .75;
    color: black;
}


.mainBanner {
    position: relative;

    .slick-track {
        position: relative;

        overflow: hidden;

        height: 600px;

        .slick-slide {
            position: relative;
            z-index: 1;

            overflow: hidden;

            width: 100%;
            height: 100%;

            outline: none;
            background-position: 50%;
            background-size: cover;

            .mainBanner-container {
                position: absolute;
                z-index: 2;
                top: 0;
                left: 0;

                width: 100%;
                height: 100%;

                outline: none;
                background: transparent;

                .mainBanner-content {
                    @extend %contained;

                    position: relative;

                    height: 100%;

                    .mainBanner-message {
                        position: absolute;

                        display: inline-block;

                        margin: 50px $spacingBase;
                        padding: $spacingXLg;

                        color: $colorBrandLight;
                        background: rgba($colorBrandDark, .85);

                        h2 {
                            margin-left: $spacingBase;
                        }
                        p,
                        h2 {
                            margin-top: 0;
                            margin-bottom: 0;
                        }
                        &--topLeft {
                            top: 0;
                            right: auto;
                            bottom: auto;
                            left: 0;
                        }
                        &--centerLeft {
                            top: 30%;
                            right: auto;
                            bottom: auto;
                            left: 0;
                        }
                        &--bottomLeft {
                            top: auto;
                            right: auto;
                            bottom: 0;
                            left: 0;
                        }
                        &--topMiddle {
                            top: 0;
                            right: auto;
                            bottom: auto;
                            left: 25%;
                        }
                        &--centerMiddle {
                            top: 30%;
                            right: auto;
                            bottom: auto;
                            left: 25%;
                        }
                        &--bottomMiddle {
                            top: auto;
                            right: auto;
                            bottom: 0;
                            left: 25%;
                        }
                        &--topRight {
                            top: 0;
                            right: 0;
                            bottom: auto;
                            left: auto;
                        }
                        &--centerRight {
                            top: 30%;
                            right: 0;
                            bottom: auto;
                            left: auto;
                        }
                        &--bottomRight {
                            top: auto;
                            right: 0;
                            bottom: 0;
                            left: auto;
                        }
                    }
                }
            }
        }
    }
    .slick-prev:before,
    .slick-next:before {
        color: black;
    }
}

.staffSlider {
    position: relative;

    overflow: visible;

    height: 350px;

    .slick-list {
        height: 100%;
    }
    .slick-slide {
        @extend %flex;

        position: relative;

        flex-shrink: 0;

        height: 225px;

        outline: none;

        a {
            @extend %flex;

            @include transition(color);

            justify-content: center;

            height: 225px;

            color: darken($colorBrandBlue, 10%);
            outline: none;

            &:hover {
                color: darken($colorBrandBlue, 30%);
            }
            img {
                width: 225px;
                height: 225px;

                border-radius: 100%;
                outline: none;
            }
            .staff-name {
                position: absolute;
                top: 100%;
                left: 0;

                width: 100%;
                margin-top: $spacingBase;

                text-align: center;

                h4 {
                    margin: 0;
                }
            }
        }
    }
}
