.locationInfo {
    h4 {
        margin-bottom: 0;
    }
    p {
        @extend .h-pushLgLeft;

        @include fontSize(16px, false);
        line-height: 1.8;

        &.subtle {
            @include fontSize(12px);
            margin: 0;
            margin-left: $spacingBase;
        }
        span {
            font-weight: bold;
            margin-right: $spacingSm;
        }
    }
    &--address {
        h4 {
            &:before {
                @extend %iconStyle;

                content: '\e807';
            }
        }
    }
    &--hours {
        h4 {
            &:before {
                @extend %iconStyle;

                content: '\e80b';
            }
        }
    }
}
