.testPallet {
    display: inline-block;

    width: 60px;
    height: 60px;
    margin: 20px 10px;
    padding: 0;

    &--white {
        background: $colorWhite;
    }

    &--greyLightest {
        background: $colorGreyLightest;
    }

    &--greyLighter {
        background: $colorGreyLighter;
    }

    &--greyLight {
        background: $colorGreyLight;
    }

    &--grey {
        background: $colorGrey;
    }

    &--greyDark {
        background: $colorGreyDark;
    }

    &--greyDarker {
        background: $colorGreyDarker;
    }

    &--greyDarkest {
        background: $colorGreyDarkest;
    }

    &--black {
        background: $colorBlack;
    }

    &--yellow {
        background: $colorBrandYellow;
    }

    &--blue {
        background: $colorBrandBlue;
    }

    &--dark {
        background: $colorBrandDark;
    }

    &--mid {
        background: $colorBrandMid;
    }

    &--light {
        background: $colorBrandLight;
    }
}
