/* Google Web Fonts */
@import url(https://fonts.googleapis.com/css?family=Droid+Serif:400,700|Montserrat:400,700);

/* Slick Icons */
@font-face {
    font-family: 'slick';
    font-weight: normal;
    font-style: normal;

    src: url('../fonts/slick.eot');
    src: url('../fonts/slick.eot?#iefix') format('embedded-opentype'), url('../fonts/slick.woff') format('woff'), url('../fonts/slick.ttf') format('truetype'), url('../fonts/slick.svg#slick') format('svg');
}

/* Page Icons */
@font-face {
    font-family: 'fontello';
    font-weight: normal;
    font-style: normal;

    src: url('../fonts/fontello.eot?77034071');
    src: url('../fonts/fontello.eot?77034071#iefix') format('embedded-opentype'),
    url('../fonts/fontello.woff2?77034071') format('woff2'),
    url('../fonts/fontello.woff?77034071') format('woff'),
    url('../fonts/fontello.ttf?77034071') format('truetype'),
    url('../fonts/fontello.svg?77034071#fontello') format('svg');
}