.headerMain {
    @extend %flex;

    flex: 0 0 auto;

    width: 100%;

    font-family: $fontHighlight;

    color: $colorBrandDark;
    background: $colorBrandLight;

    .headerMain-content {
        @extend %contained;
        @extend %flex;

        align-items: center;
        justify-content: space-between;

        padding: 0 $spacingBase;

        .headerMain-logo {
            h1 {
                margin-bottom: 0;
                text-shadow: 1px 1px 1px rgba(127, 127, 127, .6);
                span {
                    margin-right: 15px;
                    color: $colorBrandYellow;
                    &:first-child {
                        color: $colorBrandBlue;
                    }
                    &:last-child {
                        display: block;
                        margin-left: $spacingLg;
                        color:$colorBrandDark;
                    }
                }
            }
        }
        .headerMain-utilityNav {
            @extend %flex;

            align-self: flex-start;

            border-radius: 0 0 5px 5px;
            background: $colorBrandDark;

            nav {
                @extend %flex;

                padding: $spacingXSm;

                a {
                    @include transition(color);

                    display: block;

                    padding: $spacingXSm $spacingBase;

                    text-decoration: none;
                    font-size: $fontSizeXSm;
                    font-weight: lighter;
                    line-height: $fontSizeSm;

                    color: $colorWhite;

                    &:not(:last-child) {
                        border-right: 1px solid $colorBrandMid;
                    }
                    &:hover {
                        color: $colorBrandBlue;
                    }
                    &:before {
                        font-size: $fontSizeSm;
                        line-height: $fontSizeSm;
                    }
                }
            }
        }
    }
}
